import { ReactElement, RefObject, useEffect, useRef } from "react";
import { ngon, translate, Cubic, multiply, add } from "../../geometry";
import { IconType } from 'react-icons';

export const useNgon = (sides: number, scale:number, rotation: number, cornerRadius: number, strokeWidth: number): [number[][], Cubic, number] => {
	const [ssw, scr] = multiply([strokeWidth, cornerRadius], scale);
	const radius = scale/2;
	const [cpts, vb] = ngon(sides, radius, rotation, scr, [0, 0]);
	const hsw = ssw/2;
	const bvb: Cubic = add(vb, [-hsw, -hsw, ssw, ssw]) as Cubic;
	const points = translate(cpts, [-bvb[0], -bvb[1], -bvb[0], -bvb[1]])
	return [points, [0, 0, bvb[2], bvb[3]], ssw];
}

export const useIcon = (icon: IconType, scale:number): RefObject<SVGGElement> => {
	const ref = useRef(null);
	useEffect(()=>{
		const g = ref.current! as SVGGElement
		if(g.children.length === 0 || g.children[0].tagName !== 'svg') return;//do nothing because I wasnt trained for this
		const elems = (ref.current! as SVGGElement).children
		const elem = elems[0];
		elem.removeAttribute('width');
		elem.removeAttribute('height');
		const vb = (elem.getAttribute('viewBox') ?? '0 0 0 0').split(' ').map(parseFloat);
		const mv = Math.max(...vb);
		const mx = vb[2]/mv * scale;
		const my = vb[3]/mv * scale;
		g.setAttribute('transform',`scale(${scale}) translate(0, 0)`);
		console.log(vb, mv);
	}, [icon]);
	return ref;
}